import React from 'react';
import { navigateTo } from 'gatsby-link';
import PropTypes from 'prop-types';

import pic01 from '../images/pic01.jpg';
import pic02 from '../images/pic02.jpg';
import pic03 from '../images/pic03.jpg';

class Intro extends React.Component {
  componentDidMount() {
    this.props.onPageIn();
  }

  navigateWithTimeout = (to) => {
    this.props.onPageOut();
    setTimeout(() => {
        navigateTo(to);
    }, 175);
  }

  render() {

    let close = <div className="close" onClick={
                  () => {
                    this.navigateWithTimeout('/');
                  }
                }>
                </div>;

    return (
      <article id="intro" className={`active ${this.props.articleTimeout ? 'timeout' : ''}`} style={{display:'none'}}>
        <h2 className="major">Intro</h2>
        <span className="image main"><img src={pic01} alt="" /></span>
        <p>Hi there, I'm David and welcome to my site. In here, you can find By the way, check out my <span className="link" onClick={() => this.navigateWithTimeout('/work')}>awesome work</span>.</p>
        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Duis dapibus rutrum facilisis. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Etiam tristique libero eu nibh porttitor fermentum. Nullam venenatis erat id vehicula viverra. Nunc ultrices eros ut ultricies condimentum. Mauris risus lacus, blandit sit amet venenatis non, bibendum vitae dolor. Nunc lorem mauris, fringilla in aliquam at, euismod in lectus. Pellentesque habitant morbi tristique senectus et netus et malesuada fames ac turpis egestas. In non lorem sit amet elit placerat maximus. Pellentesque aliquam maximus risus, vel sed vehicula.</p>
        {close}
      </article>
    )
  }
}

Intro.propTypes = {
  route: PropTypes.object,
  article: PropTypes.string,
  articleTimeout: PropTypes.bool,
  onCloseArticle: PropTypes.func,
  timeout: PropTypes.bool
}

export default Intro;


